.gallery {
  flex-wrap: wrap;
  gap: 30px;
  margin-top: 100px;
  display: flex;
}

.photo-card {
  width: calc(20% - 4px);
  min-width: 300px;
  border: 2px solid red;
  border-radius: 20px;
  padding: 10px;
}

.card-image {
  width: 80%;
  margin: 0 auto;
  display: block;
}

.info {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-item:not(:last-child) {
  margin-right: 10px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #6a036a;
  border: 2px solid purple;
  border-radius: 7px;
  margin: 30px auto 0;
  padding: 15px 40px;
  display: block;
}

.load-more:disabled {
  background-color: rgba(106, 3, 106, .3);
  border-color: rgba(106, 3, 106, .3);
}

.load-more.hidden {
  display: none;
}

